
<template>
  <div class="chat">
    <error-page v-if="possibleError"/>
    <v-container 
      fluid
      v-else  
    >
      <chat-area 
        :messages="dispatchedMessages"
        :is-last-msg="isLastMessage"
        @send-message="sendMessage"
      />
      <div>
        <v-textarea
          v-model="inputValue"
          filled
          label="Type your message"
          :disabled="dispatchedMessages.length === 0 || isLastMessage"
          @keyup.enter="sendMessage"
        />
        <div class="d-flex justify-end">
          <chat-button 
            v-if="isLastMessage"
            text="Start new chat"
            @on-click="cleanAndStartNewChat"
          />
          <chat-button 
            v-else
            :disabled="isDisabledSendBtn"
            :text="btnText"
            @on-click="sendMessage"
          />

        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import ChatButton from './components/ChatButton'
import ChatArea from './components/ChatArea'
import ErrorPage from './components/ErrorPage'
import axios from 'axios';

export default {
  name: 'Chat',
  components: {
    ChatButton,
    ChatArea,
    ErrorPage
  },
  data: () => ({
    inputValue: '',
    messages: [],
    actionCount: 0,
    dispatchedMessages: [],
    possibleError: false
  }),
  created() {
    axios
      .get('messages.json')
      .then(res => this.messages = res.data)
      .catch(() => this.possibleError = true);
  },
  computed: {
    isLastMessage() {
      return this.actionCount + 1 === this.messages.length;
    },
    btnText() {
      return this.dispatchedMessages.length ? 'Send Message' : 'Let\'s chat'
    },
    isDisabledSendBtn() {
      const isFilledInput = this.inputValue.trim() !== '';
      const areExistMessages = this.dispatchedMessages.length > 0;

      return !isFilledInput && areExistMessages;
    }
  },
  methods: {
    sendMessage() {
      if (!this.dispatchedMessages.length) {
        this.dispatchedMessages.push(this.messages[this.dispatchedMessages.length]);

        if (!this.messages[this.actionCount].ask) {
          this.actionCount += 1;
          this.dispatchedMessages.push(this.messages[this.actionCount]);
        }

      } else {
        const answer = {
          text: this.inputValue,
          owner: 'me'
        };

        this.actionCount += 1;
        this.dispatchedMessages.push(answer);
        this.dispatchedMessages.push(this.messages[this.actionCount]);

        this.inputValue = '';

        if (!this.messages[this.actionCount].ask && !(this.actionCount + 1 === this.messages.length)) {
          this.actionCount += 1;
          this.dispatchedMessages.push(this.messages[this.actionCount]);
        }
      }
    },
    cleanAndStartNewChat() {
      this.dispatchedMessages = [];
      this.actionCount = 0
    }
  }
}
</script>
